<template>
    <div class="support-discussion">
        <div class="support-discussion__container">
            <back-link class="back-link" to="/my/support">
                {{ $t('BACK_TO_SUPPORT_TICKETS') }}
            </back-link>

            <div v-if="ticketLoading" class="support-discussion__loading">
                <Loader />
            </div>
            <div v-else-if="ticket">
                <!-- Ticket Moderation Section -->
                <div v-if="ticket.is_moderator === 1" class="ticket-moderation">
                    <div class="ticket-moderation__header">
                        <h3 class="ticket-moderation__title">Ticket moderation</h3>
                        <button class="close-ticket-btn" @click="showCloseModal = true">Close ticket</button>
                    </div>
                    <div class="ticket-moderation__info">
                        <div class="ticket-moderation__email">Email: {{ ticket.email }}</div>
                    </div>
                    <div class="ticket-moderation__devices">
                        <table class="devices-table">
                            <tr v-for="device in ticket.devices" :key="device.provisioning_udid">
                                <td>
                                    <a :href="`https://appdb.to/?lt=${device.link_token}`" target="_blank">
                                        Device: {{ device.nice_name }} {{ device.os_version }}
                                    </a> <span class="provisioning-udid">{{
                                        device.provisioning_udid }}</span>
                                </td>
                            </tr>
                        </table>
                    </div>
                </div>

                <div class="support-discussion__header">
                    <h1 class="support-discussion__title">{{ ticket.topic }}</h1>
                    <div class="support-discussion__meta">
                        <div class="support-discussion__date">
                            {{ $t('CREATED') }}: {{ formatDate(ticket.added_at) }}
                        </div>
                        <div class="support-discussion__status"
                            :class="{ 'support-discussion__status--closed': ticket.status === 'closed' }">
                            {{ ticket.status_translated }}
                        </div>
                    </div>
                </div>
            </div>

            <div class="support-discussion__content">
                <Discussion v-if="discussionTopic" :topic="discussionTopic" :isPrivate="true" :forceType="'private'"
                    :canCommentAsUser="!!$store.state.customerProfile?.email"
                    :canCommentAsStaff="!!$store.state.publisherProfile?.email" :disallowNestedComments="true" />
                <div v-else class="support-discussion__loading">
                    <Loader />
                </div>
            </div>
        </div>

        <!-- Close Ticket Modal -->
        <div v-if="showCloseModal" class="modal-backdrop" @click.self="showCloseModal = false">
            <div class="modal-content">
                <h3 class="modal-title">Close Ticket</h3>
                <p class="modal-message">Are you really want to close this ticket?</p>

                <div class="modal-checkbox">
                    <input type="checkbox" id="deleteTicket" v-model="deleteTicket">
                    <label for="deleteTicket">Delete it as nonsense</label>
                </div>

                <div class="modal-actions">
                    <button class="modal-cancel" @click="showCloseModal = false">Cancel</button>
                    <button class="modal-confirm" @click="closeTicket">Confirm</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Discussion from '@/components/Discussion.vue';
import BackLink from '@/components/BackLink.vue';
import Loader from '@/components/Loader.vue';

export default {
    name: 'SupportDiscussion',
    components: {
        Discussion,
        BackLink,
        Loader
    },
    metaInfo() {
        const ticketText = this.ticket ? this.$t('SUPPORT_TICKET_WITH_ID', { id: this.ticketId, topic: this.ticket.topic }) : this.$t('SUPPORT_TICKET');
        return {
            title: ticketText,
            meta: [
                {
                    name: 'description',
                    content: this.$t('SUPPORT_TICKET_META_DESCRIPTION'),
                }
            ]
        };
    },
    data() {
        return {
            discussionTopic: null,
            ticket: null,
            ticketLoading: true,
            pollTimeout: null,
            isPolling: false,
            showCloseModal: false,
            deleteTicket: false,
            closingTicket: false
        };
    },
    computed: {
        ticketId() {
            const match = this.$route.path.match(/\/my\/support\/discussion=(\d+)/);
            return match ? parseInt(match[1], 10) : null;
        }
    },
    methods: {
        formatDate(timestamp) {
            if (!timestamp) return '';
            const date = new Date(timestamp * 1000);
            return date.toLocaleString();
        },

        async fetchTicketDetails() {
            if (!this.ticketId) {
                this.$router.push('/my/support');
                return;
            }

            this.ticketLoading = true;

            try {
                const response = await this.$api.getSupportTickets(this.ticketId);

                if (response.success && response.data && response.data.length > 0) {
                    this.ticket = response.data[0];
                    this.discussionTopic = `support_${this.ticketId}`;
                } else {
                    this.$notify({
                        type: 'error',
                        title: this.$t('ERROR'),
                        text: this.$t('TICKET_NOT_FOUND')
                    });
                    this.$router.push('/my/support');
                }
            } catch (error) {
                console.error('Error fetching ticket details:', error);
                this.$notify({
                    type: 'error',
                    title: this.$t('ERROR'),
                    text: this.$t('FAILED_TO_LOAD_TICKET_DETAILS')
                });
                this.$router.push('/my/support');
            } finally {
                this.ticketLoading = false;
            }
        },

        closeTicket() {
            if (this.closingTicket) return;
            this.closingTicket = true;

            this.$api.post('close_support_ticket', {
                id: this.ticketId,
                st: this.$store.state.st,
                with_deletion: this.deleteTicket ? 1 : 0
            })
                .then(() => {
                    this.$notify({
                        type: 'success',
                        title: 'Success',
                        text: 'Ticket has been closed successfully'
                    });

                    // Update the local ticket status
                    this.ticket.status = 'closed';
                    this.ticket.status_translated = 'Closed';

                    // Close the modal
                    this.showCloseModal = false;
                    this.deleteTicket = false;

                    // If ticket was deleted, go back to support page
                    if (this.deleteTicket) {
                        this.$router.push('/my/support');
                    }
                })
                .catch(error => {
                    console.error('Error closing ticket:', error);
                    this.$notify({
                        type: 'error',
                        title: this.$t('ERROR'),
                        text: 'Failed to close the ticket. Please try again.'
                    });
                })
                .finally(() => {
                    this.closingTicket = false;
                });
        },

        async fetchMessages() {
            if (!this.discussionTopic) return;

            try {
                const response = await this.$api.getMessages({
                    topic: this.discussionTopic,
                    lt: this.$store.state.lt
                });

                // Find the Discussion component and update its messages directly
                const discussionComponent = this.$children.find(child => child.$options.name === 'Discussion');
                if (discussionComponent) {
                    discussionComponent.messages = response.data || [];
                    discussionComponent.messagesLoaded = true;
                    discussionComponent.total = response.total || 0;
                }

                // Refresh device info
                await this.$store.dispatch('getDevices');

                // Schedule next poll if we're still polling
                if (this.isPolling) {
                    this.pollTimeout = setTimeout(() => {
                        this.fetchMessages();
                    }, 15000);
                }
            } catch (error) {
                console.error('Error fetching messages:', error);
                // Stop polling on error
                this.isPolling = false;
                clearTimeout(this.pollTimeout);
            }
        },

        startPolling() {
            this.isPolling = true;
            this.fetchMessages();
        },

        stopPolling() {
            this.isPolling = false;
            clearTimeout(this.pollTimeout);
        }
    },
    mounted() {
        this.fetchTicketDetails();
        this.startPolling();
    },
    beforeDestroy() {
        this.stopPolling();
    }
};
</script>

